import React from "react";
// import { useDispatch } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  // const dispatch = useDispatch();
  // const [isDisplayProgress, setIsDisplayProgress] = React.useState(false);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    // {
    //   dataField: "warehouseCode",
    //   text: "Warehouse",
    //   type: "string",
    //   sort: true,
    // },
    { dataField: "areaName", text: "Area", type: "string", sort: true },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    // {
    //   dataField: "ripenessLevel",
    //   text: "Ripeness",
    //   type: "string",
    //   sort: true,
    // },
    { dataField: "qty", text: "Input Weight", type: "number", sort: true },
    { dataField: "newGrade", text: "Grade", type: "string", sort: true },
    {
      dataField: "newRipeness",
      text: "Ripeness",
      type: "string",
      sort: true,
    },
  ];

  const dataSet1 = [
    {
      warehouse: "",
      area: "",
      po: "",
      skuNumber: "",
      expiryDate: "",
      qty: "",
      grade: "",
      ripeness: "",
    },
  ];

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="StockTakeUpload_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          {/* <ExcelColumn label="Warehouse" value="warehouse" /> */}
          <ExcelColumn label="Area" value="area" />
          <ExcelColumn label="PO SKU Code" value="po" />
          <ExcelColumn label="SKU Number" value="skuNumber" />
          <ExcelColumn label="Expiry Date" value="expiryDate" />
          <ExcelColumn label="Input Weight" value="qty" />
          <ExcelColumn label="Grade" value="grade" />
          <ExcelColumn label="Ripeness" value="ripeness" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push("/inventory-entry/stock-take/CSV-upload/review")
        }
        transaction={"stock_take"}
        field={Field}
      />
    </div>
  );
};

export default withRouter(Upload);
